import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, AlertController, ToastController, LoadingController } from 'ionic-angular';
import { HkApiproviderProvider } from '../../providers/hk-apiprovider/hk-apiprovider';

@IonicPage({
  name: 'CategoryPage',
  segment: 'category',
  defaultHistory: ['HomePage']
})
@Component({
  selector: 'page-category',
  templateUrl: 'category.html',
})
export class CategoryPage {

  postData = {
    "user_id": "",
    "token": "",
  }

  baseUrlImage: any;
  itemsuperqty: number;
  userDetails: any;
  resposeData: any;
  categoryFullData: any;
  year: number;

  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    public alertCtrl: AlertController,
    public auth: HkApiproviderProvider,
    public toastController: ToastController,
    public loadingCtrl: LoadingController,
  ) {

    this.itemsuperqty = 1;
    this.baseUrlImage = auth.getimage();
    if (localStorage.getItem('userData')) {
      const data = JSON.parse(localStorage.getItem('userData'));
      this.userDetails = data.userData;
    }
    const date =  new Date();
     this.year =  date.getFullYear();
    this.postData.token = this.auth.gettoken();
    this.getcategorydata();
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad CategoryPage');
  }


  public getcategorydata() {
    let zest = this.loadingCtrl.create({
      content: "Getting Data..",
      duration: 20000,
      spinner: 'crescent'
    });

    zest.present();

    this.auth.postData(this.postData, "getcategory").then((result) => {
      this.resposeData = result;
      this.categoryFullData = this.resposeData.Data;
      console.log(this.categoryFullData);
      zest.dismiss();
    }, (err) => {
      zest.dismiss();
    });
  }

  opencategory(category, cname) {
    this.navCtrl.push('ProductsPage', {
      category: category,
      cname: cname
    });
  }
  openUrl(url){
    window.open(url)
    // let parseUrl:any =  this.sanitizer.bypassSecurityTrustResourceUrl(url)
    // window.open(parseUrl);
  }

  onclickHome() {
    this.navCtrl.setRoot('HomePage');
  }

  openContact() {
    this.navCtrl.push('ContactPage');
  }

  openfeedback() {
    this.navCtrl.push('FeedbackPage');
  }
  openterms() {
    this.navCtrl.push('TermsPage');
  }

  openprivacy() {
    this.navCtrl.push('TermsPage');
  }
  
  checkLogin() {
    if (localStorage.getItem('userData')) {
      return true;
    }
    return false;
  }
}



